import React from 'react'
import Link from 'gatsby-link'

import Header from '../components/Header/index'

const Story5 = () => (
    <div className="container">
        <Header title="殡仪馆旁边的隧道" back='/day-1/' />
        <div className="content">
            <p>
                平时你们是不是觉得那(gui)在讲讲厦门其他灵异的地方，靠南普陀那里有一个地方叫大生里，以前是殡仪馆，旁边有个隧道。那里很少人去那里相传隧道里有个卖扁食的老奶奶，可是她没头因为她头被火车撞没了，没了头依旧在那卖扁食，现在已经不通火车了，你敢去的话可以去看看。 大生里的殡仪馆虽然不烧尸体了可是还依旧在那里。现在很多新人拍婚纱照，选景不了解，花大钱去鬼屋墓地拍照看了真是心惊胆战的，接下来说说大家喜欢选景的地方之一。接着拍景圣地之一就忠仑公园，它的前身是也墓地山。
                </p>
        </div>
    </div>
)

export default Story5



